import React, { Component } from "react";
import image from "../img/9joueurs.png";
import Result from "./Result";
import Data from "./data.json";

class Screen9 extends Component {
  state = {
    blinde: null,
    position: null,
    asked: null,
    resul: null,
    pourcent: "",
  };

  render() {
    return (
      <div>
        <div className="d-flex justify-content-around align-items-center">
          <div className="col-6">
            <img src={image} alt="" width="100%" />
          </div>
          <div className="col-6 d-flex flex-column text-white">
            <form
              onSubmit={(event) => {
                event.preventDefault();
                const asked = this.state.blinde + this.state.position;
                const state_copy = { ...this };
                state_copy.asked = asked;
                this.setState(state_copy);
                const data = Data;
                data.forEach((element) => {
                  console.log(element.asked);
                  if (element.asked === asked) {
                    console.log("elems", element.value);
                    const state_copy2 = { ...this };
                    state_copy2.resul = element.value;
                    state_copy2.pourcent = element.pourcent;
                    this.setState(state_copy2);
                  }
                });
              }}
            >
              <label className="h2">Sélectionnez votre nombre de blinde:</label>
              <select
                required
                className="custom-select w-75"
                onChange={() => {
                  const blindes = document.querySelectorAll(".optionsB");
                  for (const blinde of blindes) {
                    if (blinde.selected === true) {
                      const blindv = blinde.value;
                      const state_copy = { ...this.state };
                      state_copy.blinde = blindv;
                      this.setState(state_copy);
                    }
                  }
                }}
              >
                <option className="optionsB" value="1" id="1">
                  1
                </option>
                <option className="optionsB" value="2" id="2">
                  2
                </option>
                <option className="optionsB" value="3" id="3">
                  3
                </option>
                <option className="optionsB" value="4" id="4">
                  4
                </option>
                <option className="optionsB" value="5" id="5">
                  5
                </option>
                <option className="optionsB" value="6" id="6">
                  6
                </option>
                <option className="optionsB" value="7" id="7">
                  7
                </option>
                <option className="optionsB" value="8" id="8">
                  8
                </option>
                <option className="optionsB" value="9" id="9">
                  9
                </option>
                <option className="optionsB" value="10" id="10">
                  10
                </option>
                <option className="optionsB" value="11" id="11">
                  11
                </option>
                <option className="optionsB" value="12" id="12">
                  12
                </option>
                <option className="optionsB" value="13" id="13">
                  13
                </option>
                <option className="optionsB" value="14" id="14">
                  14
                </option>
                <option className="optionsB" value="15" id="15">
                  15
                </option>
                <option className="optionsB" value="16" id="16">
                  16
                </option>
              </select>
              <label className="h2">Sélectionnez votre position:</label>
              <select
                required
                className="custom-select w-75"
                onChange={() => {
                  const positions = document.querySelectorAll(".optionsP");
                  for (const pos of positions) {
                    if (pos.selected === true) {
                      const posv = pos.value;
                      const state_copy = { ...this.state };
                      state_copy.position = posv;
                      this.setState(state_copy);
                    }
                  }
                }}
              >
                <option className="optionsP" value="Btn" id="btn">
                  Btn
                </option>
                <option className="optionsP" value="SB" id="SB">
                  SB
                </option>
                <option className="optionsP" value="Utg" id="utg">
                  Utg
                </option>
                <option className="optionsP" value="Utg1" id="utg1">
                  Utg1
                </option>
                <option className="optionsP" value="Utg2" id="utg2">
                  Utg2
                </option>
                <option className="optionsP" value="Lj" id="lj">
                  Lj
                </option>
                <option className="optionsP" value="Hj" id="hj">
                  Hj
                </option>
                <option className="optionsP" value="Co" id="co">
                  Co
                </option>
              </select>
              <button className="btn btn-success ml-5" type="submit">
                Valider
              </button>
            </form>
          </div>
        </div>
        <div>
          <Result
            asked={this.state.asked}
            resul={this.state.resul}
            pourcent={this.state.pourcent}
          />
        </div>
      </div>
    );
  }
}

export default Screen9;
